import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { DenunciaformComponent } from './denunciaform/denunciaform.component';
import { FormularioComponent } from './denunciaform/formulario/formulario.component';
import { SeguimientoComponent } from './seguimiento/seguimiento.component';
import {RegistroComponent } from './denunciaform/formulario/registro/registro.component';
import { EstadoComponent } from './seguimiento/estado/estado.component';


const routes: Routes = [

  { path: '', component: HomeComponent },

  { path: 'formulariodenuncia', component: DenunciaformComponent},
  { path: 'formulariodenuncia/formulario', component: FormularioComponent},
  { path: 'formulariodenuncia/formulario/registro', component: RegistroComponent},

  { path: 'seguimiento', component: SeguimientoComponent},
  { path: 'seguimiento/estado', component: EstadoComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes,  {
    scrollPositionRestoration: 'enabled',
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
