import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-denunciaform',
  templateUrl: './denunciaform.component.html',
  styleUrls: ['./denunciaform.component.scss']
})
export class DenunciaformComponent implements OnInit {

  constructor( public router: Router ) { }

  aviso_privacidad: boolean = false;

  ngOnInit(): void {
  }

  siguiente(){
    if (this.aviso_privacidad == false) {
      Swal.fire("Check!","Acepte las condiciones anunciadas en el aviso de privacidad","error")
    }
    else{ this.router.navigate(['/formulariodenuncia/formulario']);}
  }

}
